export default {
	components: {
	},
	props: [
		'value',
		'type',
		'placeholder',
		'placeholderStyle',
		'placeholderClass',
		'disabled',
		'focus',
		'cursor',
		'adjustPosition',
		'maxlength',
		'tTColor',
		'tTWidth',
		'tTHeight',
		'tTMinHeight',
		'tTFontSize',
		'tTLineHeight',
		'autoHeight',
		'tTTAlign',
		'tTPadding',
		'tTMargin',
		'tTBG',
		'tTBorder',
		'tTBStyle',
		'maxNumShow',
		'tTBoxMargin'
		],
	data() {
		return {
			tTMargins: '1px',
			tTBGs: '', // 默认
			tTBorders: '', // 默认
			tTBStyles: '', // 默认
			valueText: this.value
		};
	},
	created() {
		if(this.tTBoxMargin){
			this.tTMargins = this.tTBoxMargin;
		}
	},
	watch: {
		value: {
			handler(a, b) {
				console.log(a, b, '--------------------------------------监听valueText变化---------------------------------------');
				this.valueText = a;
			},
			deep: true
		}
	},
	methods: {
		// 当键盘输入时，触发input事件
		onInput(e) {
			console.log(e, 'textAarea返回值');
			this.valueText = e.detail.value;
		},
		// 输入框聚焦时触发
		onFocus(e) {
			console.log(e, '触发焦点，进入编辑模式');
			if(!this.tTBoxMargin) { // 如果不存在用户自定义样式
				this.tTMargins = '';
				this.tTBGs = this.tTBG;
				this.tTBorders = this.tTBorder;
				this.tTBStyles = this.tTBStyle;
			}
		},
		// 输入框失去焦点时触发
		onBlur(e) {
			console.log(e, 'textAarea返回值');
			this.valueText = e.detail.value;
			this.$emit('tab-TextArea',this.valueText);
			console.log(e, '失去焦点，进入预览模式');
			if(!this.tTBoxMargin) { // 如果不存在用户自定义样式
				this.tTMargins = '1px';
				this.tTBGs = '';
				this.tTBorders = '';
				this.tTBStyles = '';
			}
		},
		// 点击完成按钮时触发
		onConfirm(e) {
			console.log(e, 'textAarea返回值');
			this.valueText = e.detail.value;
			this.$emit('tab-TextArea',this.valueText);
			console.log(e, '失去焦点，进入预览模式');
			if(!this.tTBoxMargin) { // 如果不存在用户自定义样式
				this.tTMargins = '1px';
				this.tTBGs = '';
				this.tTBorders = '';
				this.tTBStyles = '';
			}
		},
	}
};
